@import url(https://fonts.googleapis.com/css?family=Fira+Sans:400,500,600,700,800);
* {
  box-sizing: border-box;
}
body {
  background-image: url("https://images.pexels.com/photos/1831234/pexels-photo-1831234.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940");
  display: flex;
  font-weight: 400;
  font-family: 'Fira Sans', sans-serif;
}

h1,h2,h3,h4,h5,h6,label,span {
  font-weight: 500;
  font-family: 'Fira Sans', sans-serif;
}

body, html, .App, #root, .auth-wrapper {
  width: 100%;
  height: 100%;
}

.navbar-light {
  background-color: #ffffff;
  opacity: 75% !important;
  box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.2);
}
.navbar {
  min-width: 100vh;
}

.auth-wrapper {
  display: flex;
  justify-content: center;
  flex-direction: column;
  text-align: left;
}

.auth-inner {
  width: 450px;
  margin: auto;
  background: #ffffff;
  box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.2);
  padding: 40px 55px 45px 55px;
  border-radius: 15px;
  transition: all .3s;
}

.auth-wrapper .form-control:focus {
  border-color: #F183D0;
  box-shadow: none;
}

.auth-wrapper h3 {
  text-align: center;
  margin: 0;
  line-height: 1;
  padding-bottom: 20px;
}

.custom-control-label {
  font-weight: 400;
}

.buttom {
  color: #ffffff;
  background-color: #F183D0;
  border-color: #F183D0;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}
 
.btn-primary {
  background-color: #fa9ed1;
  color: #fff;
  border-color: #f883c4;
}
.btn-primary:hover {
  background-color: #fa8bc8;
  color: #fff;
  border-color: #fc6aba;
}

.btn-primary:not(:disabled):not(.disabled):active{
  background-color: #f876be;
  color: #fff;
  border-color: #fc6aba;
}

.btn-primary:focus {
  background-color: #f876be;
  color: #fff;
  border-color: #fc6aba;
}

.error {
  font-size: 12px;
  font-weight: lighter;
}
